import * as THREE from 'three';
import assets from '../lib/AssetManager';

//
// ENVIRONMENTAL MAP
//

export const HdriPink = new THREE.CubeTextureLoader()
  .setPath('assets/textures/skybox/skyboxPink/')
  .load([
    'px.png', // right
    'nx.png', // left
    'py.png', // up
    'ny.png', // down
    'pz.png', // back
    'nz.png', //fwd
  ]);

//
// TEXTURES
//

