import * as THREE from 'three';
import assets from '../../lib/AssetManager';

import {
  MAT_NeonWhite,
  MAT_Flower,
  MAT_Building,
  MAT_StairPillar,
  MAT_EHJwall,
  MAT_EHJinlay,
  MAT_Marble,
  MAT_MatteBlack,
  MAT_WhiteSteel,
  MAT_SteelFrame,
  // thumbnails
  // MAT_TNdisability,
  // MAT_TNehjMain,
  // MAT_TNehjMini,
  // MAT_TNfruity,
  // MAT_TNtoday,
  // MAT_TNtogether,
} from '../Materials';

const galleryKey = assets.queue({
  url: 'assets/models/Gallery.glb',
  type: 'gltf',
});

export default class Gallery extends THREE.Group {
  constructor(webgl) {
    super();

    this.galleryGltf = assets.get(galleryKey);

    this.add(this.galleryGltf.scene);
    // this.scale.multiplyScalar(0.1);
  }

  setup() {
    // temp place to load textures & materials
    // VIDEO
    const T_TNfruity_BCKey = new THREE.TextureLoader().load(
      'assets/textures/videoTN/tnfruity.jpg'
    );

    const T_TNtogether_BCKey = new THREE.TextureLoader().load(
      'assets/textures/videoTN/tntogether.jpg'
    );

    const T_TNtoday_BCKey = new THREE.TextureLoader().load(
      'assets/textures/videoTN/tntoday.jpg'
    );

    const T_TNdisability_BCKey = new THREE.TextureLoader().load(
      'assets/textures/videoTN/tndisability.jpg'
    );

    const T_TNehjMain_BCKey = new THREE.TextureLoader().load(
      'assets/textures/videoTN/tnehjmain.jpg'
    );

    const T_TNehjMini_BCKey = new THREE.TextureLoader().load(
      'assets/textures/videoTN/tnehjmini.jpg'
    );

    const MAT_TNfruity = new THREE.MeshStandardMaterial({
      name: 'TNfruity',
      // color: 'hsl(0,0%,100%)',
      map: T_TNfruity_BCKey,
      roughness: 0.85,
      metalness: 0,
    });

    const MAT_TNehjMain = new THREE.MeshStandardMaterial({
      name: 'TNehjMain',
      // color: 'hsl(0,0%,100%)',
      map: T_TNehjMain_BCKey,
      roughness: 0.85,
      metalness: 0,
    });

    const MAT_TNehjMini = new THREE.MeshStandardMaterial({
      name: 'TNehjMini',
      // color: 'hsl(0,0%,100%)',
      map: T_TNehjMini_BCKey,
      roughness: 0.85,
      metalness: 0,
    });

    const MAT_TNtogether = new THREE.MeshStandardMaterial({
      name: 'TNtogether',
      // color: 'hsl(0,0%,100%)',
      map: T_TNtogether_BCKey,
      roughness: 0.85,
      metalness: 0,
    });

    const MAT_TNtoday = new THREE.MeshStandardMaterial({
      name: 'TNtoday',
      // color: 'hsl(0,0%,100%)',
      map: T_TNtoday_BCKey,
      roughness: 0.85,
      metalness: 0,
    });

    const MAT_TNdisability = new THREE.MeshStandardMaterial({
      name: 'TNfruity',
      // color: 'hsl(0,0%,100%)',
      map: T_TNdisability_BCKey,
      roughness: 0.85,
      metalness: 0,
    });

    this.galleryGltf.scene.traverse((child) => {
      // SET MATERIALS
      if (child.material)
        switch (child.material.name) {
          case 'Neon':
            child.material = MAT_NeonWhite;
            break;
          case 'Flower':
            child.material = MAT_Flower;
            break;
          // case 'Building':
          //   child.material = MAT_Building;
          //   break;
          case 'Stair-Pillar':
            child.material = MAT_StairPillar;
            break;
          // EHJ
          case 'Car Paint - Black':
            child.material = MAT_EHJwall;
            break;
          case 'LED Yellow':
            child.material = MAT_EHJinlay;
            break;
          // MIRROR
          case 'steelframe1':
            child.material = MAT_SteelFrame;
            break;
          case 'whitesteel':
            child.material = MAT_WhiteSteel;
            break;
          case 'neon':
            child.material = MAT_NeonWhite;
            break;
          case 'marble':
            child.material = MAT_Marble;
            break;
          case 'matteblack':
            child.material = MAT_MatteBlack;
            break;
        }
      if (child.name) {
        switch (child.name) {
          case 'ScreenFruity':
            child.material = MAT_TNfruity;
            break;
          case 'ScreenTogether':
            child.material = MAT_TNtogether;
            break;
          case 'ScreenToday':
            child.material = MAT_TNtoday;
            break;
          case 'ScreenDisability':
            child.material = MAT_TNdisability;
            break;
          case 'ScreenEHJmain':
            child.material = MAT_TNehjMain;
            break;
          case 'ScreenEHJp1':
            child.material = MAT_TNehjMini;
            break;
          case 'ScreenEHJp2':
            child.material = MAT_TNehjMini;
            break;
          case 'ScreenEHJp3':
            child.material = MAT_TNehjMini;
            break;
          case 'ScreenEHJp4':
            child.material = MAT_TNehjMini;
            break;
        }
      }
      // SET INTERACTABLES
      if (child.name) {
        if (child.name.includes('Menu')) {
          webgl.interactables.push(child);
        } else if (child.name.includes('Pedestal')) {
          webgl.interactables.push(child);
        }
      }
    });
  }
}
