import * as THREE from 'three';

// TO DO
// - better normalization per fundamentals picking
// - remove touches from webgl app and implement proper touch handling
// - clear touch coord when touch ends

export default class PickHelper {
  constructor(webgl) {
    this.webgl = webgl;
    this.raycaster = new THREE.Raycaster();
    this.coords = new THREE.Vector2();
    this.pickedObject = null;
  }

  onPointerDown(event, [x, y]) {
    this.coords.set(
      (x / this.webgl.width) * 2 - 1,
      (-y / this.webgl.height) * 2 + 1
    );
    // console.log('x, y: (' + x + ',' + y + ')');
    // console.log(`transformed x, y: (${this.coords.x},${this.coords.y})`);
    this.raycaster.setFromCamera(this.coords, this.webgl.camera);
    let hits = this.raycaster.intersectObjects(this.webgl.interactables, true);

    // console.log(hits.length > 0 ? `Hit ${hits[0].object.name}!` : 'No hit');
  }

  onPointerMove(event, [x, y]) {
    this.coords.set(
      (x / this.webgl.width) * 2 - 1,
      (-y / this.webgl.height) * 2 + 1
    );

    this.raycaster.setFromCamera(this.coords, this.webgl.camera);
    const hits = this.raycaster.intersectObjects(this.webgl.interactables);
    // console.log(hits.length > 0 ? `Hit ${hits[0].object.name}!` : 'No hit');
  }

  onPointerUp(event, [x, y]) {}
}
